.row {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display:         flex;
    flex-wrap: wrap;
  }
  .row > [class*='col-'] {
    display: flex;
    flex-direction: column;
  }

.home__content{
    /*background-color: black;
    height: auto !important;
    min-height: 100vh;
    width: 100%;
    color: white;*/
    margin-top: 2%;
}
.home__card{
    margin: 0 auto;
    float: none;
    /*margin-top: 25%;*/
    min-height: 50%;
    font-size: 30px;
    font-weight: bold;
}

.profile__image{
    max-width: 64%;
    height: auto;
    display: block;
}

.profile__image__content{
    display: block;
}

.anchor-style{
    text-decoration: none;
    font-weight: small;
}

.background__content{
    font-weight: bold;
    font-size: large;    
}

.email__content{
    font-size: large;
    margin-left: 8px;
    font-weight: bold;
}

.profile__links{
    font-size: smaller;
    color: black;
}
.profile__links__email{
    font-size: 16px;
    color: black;
}